import React from "react";
import {Cell, Legend, Pie, PieChart, PieLabelRenderProps, Sector} from "recharts";
import {Box} from "@material-ui/core";
import {Wykres} from "./infografika/Wykres";
import {format} from "./dane/util";

export interface NameValue {
    name: string;
    value: number;
}

export class WykresKolowy extends React.Component<Wykres> {
    constructor(props: Wykres) {
        super(props);

        this.legendRenderer = this.legendRenderer.bind(this);
    }

    RADIAN = Math.PI / 180;
    state = {
        activeIndex: -1,
        verticalOffset: 0,
    };

    onPieClick = (data: PieLabelRenderProps, index: number) => {
        const {midAngle} = data;
        const sin = Math.sin(-this.RADIAN * (midAngle || 0));
        this.setState({
            verticalOffset: (this.props.typ_wykresu.indexOf("Kołowy") > -1 && sin < 0 && this.state.activeIndex !== index ? 80 : 0),
            activeIndex: (this.state.activeIndex === index? -1 : index),
        });
    };

    // @ts-ignore
    legendRenderer(value, entry, index) {
        if (index === this.state.activeIndex) {
            return <span><b>{value}</b></span>;
        } else {
            return <span>{value}</span>;
        }
    }

    render() {
        const {dane_kolowy = [], opis, typ_wykresu, jednostka = '', procent_calosci = 100} = this.props;

        const inside = typ_wykresu.indexOf("Pierścieniowy") > -1;
        // @ts-ignore
        const renderActiveShape = (props) => {
            const {
                cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
                percent, value,
            } = props;
            const fill = '#f9c00c';
            const sin = Math.sin(-this.RADIAN * midAngle);
            const cos = Math.cos(-this.RADIAN * midAngle);
            const osx = cx + (outerRadius + 20) * cos;
            const osy = cy + (outerRadius + 20) * sin;
            const omx = cx + (outerRadius + 60) * cos;
            const omy = cy + (outerRadius + 60) * sin;
            const oex = omx;
            const oey = cy + ((outerRadius / 0.86 + 36) * (sin >= 0 ? 1 : -1)) - 10;
            const isx = cx + (innerRadius + 9) * cos;
            const isy = cy + (innerRadius + 9) * sin;
            const imx = cx + (innerRadius / 2) * cos;
            const imy = cy + (innerRadius / 2) * sin;
            const outsideTextAnchor = cos < 0 ? 'start' : 'end';
            const line1 = format(value, jednostka);
            const line2 = (percent * 100).toFixed(2) + '%' + (procent_calosci === 100 ?'':' / ' + (1.0 * procent_calosci * percent).toFixed(2) + '%');
            return (
                <g>
                    <Sector
                        cx={cx + 12 * cos}
                        cy={cy + 12 * sin}
                        innerRadius={innerRadius}
                        outerRadius={outerRadius + 5}
                        startAngle={startAngle}
                        endAngle={endAngle}
                        fill={fill}
                        stroke="black"
                        strokeWidth={2}
                    />
                    {inside?
                        <React.Fragment>
                            <path d={`M${isx},${isy}L${imx},${imy}`} stroke={fill} strokeWidth={2} fill="none"/>
                            <circle cx={imx} cy={imy} r={4} fill={fill} stroke="none" />
                            <text x={cx} y={cy} textAnchor="middle" fill="#333">{line1}</text>
                            <text x={cx} y={cy} dy={36} textAnchor="middle" fill="#999">{line2}</text>
                        </React.Fragment>:
                        <React.Fragment>
                            <path d={`M${osx},${osy}L${omx},${omy}L${oex},${oey}`} stroke={fill} strokeWidth={2} fill="none" />
                            <circle cx={oex} cy={oey} r={4} fill={fill} stroke="none" />
                            <text x={oex + (cos < 0 ? 1 : -1) * 24} y={oey} textAnchor={outsideTextAnchor} fill="#333">{line1}</text>
                            <text x={oex + (cos < 0 ? 1 : -1) * 24} y={oey} dy={36} textAnchor={outsideTextAnchor} fill="#999">{line2}</text>
                        </React.Fragment>}
                </g>
            );
        };

        let daneFinal: any[] = dane_kolowy.slice(0, 8);
        if (dane_kolowy.length > 8) {
            daneFinal.push({name: "Pozostałe", value: dane_kolowy.slice(8).reduce((a, b) => a + b.value, 0)});
        }
        // https://learnui.design/tools/data-color-picker.html#palette
        const COLORS = ["grey", "#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"].reverse();
        // const HUE = [
        //     ["#003f5c", "#255571", "#406b88", "#59829e", "#739bb6", "#8db3ce", "#a7cde6", "#c2e7ff"],
        //     ["#2f4b7c", "#475f8e", "#5f73a0", "#7688b2", "#8d9ec5", "#a5b4d8", "#bdcbeb", "#d6e2ff"],
        // ];
        return <React.Fragment>
            {opis?<React.Fragment><br/><Box className="intro" dangerouslySetInnerHTML={{__html: opis}} /></React.Fragment>:<React.Fragment/>}
            <PieChart width={1200} height={inside?520:400 + (this.state.activeIndex >= 0 ? 80:0)}>
            <Pie data={daneFinal} dataKey="value" nameKey="name" cx="50%" cy={inside?254:200+this.state.verticalOffset}
                 innerRadius={inside?160:40} outerRadius={inside?240:172} label={false} startAngle={450} endAngle={90} activeIndex={this.state.activeIndex}
                 onClick={this.onPieClick}
                 activeShape={renderActiveShape}>
                {
                    // @ts-ignore
                    daneFinal.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={index === this.state.activeIndex?"#f9c00c":COLORS[index]}/>
                    ))
                }
            </Pie>
            <Legend layout="vertical" verticalAlign="bottom" align="right" formatter={this.legendRenderer}/>
            </PieChart>
        </React.Fragment>;
    }
}