import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { Provider } from 'react-redux'
import configureStore from './configureStore'
import { JSONUncrush } from "jsoncrush";
import axios from "axios";
import { ThemeProvider } from '@material-ui/core/styles'
import CssBaseline from '@material-ui/core/CssBaseline'
import theme from './theme'

document.title = "Generator Infografik";

const HAPPI_API_KEY = "eaa67fSr0cY475zwvjScRDs3Sj9YvPz9LUfsBiVqloMFAL2AyqNpnCxu";

const renderApp = () => ReactDOM.render(
    <React.StrictMode>
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <Provider store={store}>
                <App />
            </Provider>
        </ThemeProvider>
    </React.StrictMode>,
    document.getElementById('root')
);

const konfigurujStore = (s: string) => {
    const initialState = JSON.parse(JSONUncrush(s));
    store = configureStore(initialState);
}

let store: any;
const queryString = window.location.search;
const params = new URLSearchParams(queryString);


// Na początku próbujemy zainicjalizować stan z hash'a w URLu lub w sesji (z przekierowania z 404.html na github)
let hash = undefined;
if (sessionStorage.redirect !== undefined) {
    hash = sessionStorage.redirect.split('/')[3].split('?')[0];
} else if (params.has("p")) {
    hash = params.get("p") || '';
}

if (hash !== undefined) {
    const url = "https://api.happi.dev/v1/store/infografika.edu.pl/" + hash + "?apikey=" + HAPPI_API_KEY;
    delete sessionStorage.redirect;
    axios.get(url).then((response) => {
        if (response.data.data.value !== undefined) {
            konfigurujStore(response.data.data.value);
        } else {
            console.log(response.status);
            console.log(response.statusText);
            console.log(response.data);
        }}).catch((response) => {
            console.log(response.status);
            console.log(response.statusText);
            console.log(response.data);
            store = configureStore();
        }).finally(() => {
            renderApp();
        });
}

// Próbujemy też jeszcze z całego hash'a w URLU
if (params.has("s")) {
    const s = params.get("s");
    konfigurujStore(s || '');
    window.history.pushState({}, "Generator Infografik", '/');
    renderApp();
} else if (hash === undefined) {
    // W przeciwnym wypadku tworzymy domyślny store
    store = configureStore();
    renderApp();
}

// @ts-ignore
if (process.env.NODE_ENV !== 'production' && module.hot) {
    // @ts-ignore
    module.hot.accept('./App', renderApp)
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
