import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {changeKodGminy, changeListaPowiatow, changeWojewodztwo, pobierzListePowiatow} from "./konfiguracja/actions";
import {RootState} from "./reducers";
import {changeDaneGminy} from "./dane/actions";
import {initialDaneGminy} from "./dane/reducers";
import Dropdown from 'react-dropdown';

const WOJEWODZTWA = [
    "",
    "DOLNOŚLĄSKIE",
    "KUJAWSKO-POMORSKIE",
    "LUBELSKIE",
    "LUBUSKIE",
    "ŁÓDZKIE",
    "MAŁOPOLSKIE",
    "MAZOWIECKIE",
    "OPOLSKIE",
    "PODKARPACKIE",
    "PODLASKIE",
    "POMORSKIE",
    "ŚLĄSKIE",
    "ŚWIĘTOKRZYSKIE",
    "WARMIŃSKO-MAZURSKIE",
    "WIELKOPOLSKIE",
    "ZACHODNIOPOMORSKIE",
]

class SelektorWojewodztwaInternal extends React.Component<PropsFromRedux> {
    render() {
        const {pobierzListePowiatow, wojewodztwo, changeWojewodztwo, changeKodGminy, changeListaPowiatow, changeDaneGminy} = this.props;
        return <Dropdown
            value={wojewodztwo}
            onChange={ev => {
                changeWojewodztwo(ev.value);
                if (ev.value !== '') {
                    pobierzListePowiatow(ev.value);
                } else {
                    changeKodGminy('');
                    changeListaPowiatow([]);
                    changeDaneGminy(initialDaneGminy);
                }
            }}
            options={WOJEWODZTWA}>
        </Dropdown>;
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        wojewodztwo: state.wojewodztwo,
    }
}

const mapDispatchToProps = {
    pobierzListePowiatow,
    changeWojewodztwo,
    changeKodGminy,
    changeListaPowiatow,
    changeDaneGminy,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(SelektorWojewodztwaInternal);
