import React from "react";
// @ts-ignore
import DropdownMultiselect from "react-multiselect-dropdown-bootstrap";
import {RootState} from "./reducers";
import {connect, ConnectedProps} from "react-redux";
import {MODULY} from "./konfiguracja/reducers";
import {changeModuly} from "./konfiguracja/actions";

class WyborModulowInternal extends React.Component<PropsFromRedux> {
    render() {
        const {moduly, changeModuly} = this.props;
        return <div style={{width: "520px"}}>
            <DropdownMultiselect options={MODULY} selected={moduly} handleOnChange={(selected: string[]) => changeModuly(selected)}>
        </DropdownMultiselect></div>
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        moduly: state.moduly,
    };
};

const mapDispatchToProps = {
    changeModuly,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(WyborModulowInternal);