import {
    CHANGE_LOGO,
    CHANGE_CYTAT,
    CHANGE_AUTOR,
    CHANGE_WOJEWODZTWO,
    CHANGE_DZIAL,
    CHANGE_MODULY,
    StringAction,
    ListaPowiatowAction,
    CHANGE_LISTA_POWIATOW, CHANGE_KOD_GMINY, StringArrayAction
} from "../action";
import {PowiatEls} from "./actions";
import React from "react";

export const PREDEFINED_LOGOS = [
    {label: <React.Fragment>BRAK</React.Fragment>,
    value: "{}"},
    {label: <React.Fragment>logo_infografika.png</React.Fragment>,
        value: JSON.stringify({
            backgroundImage: "url(logo_infografika.png), " +
                "radial-gradient(circle at right, #fcd475, #ffffff)",
            backgroundPosition: "left top, right center",
            backgroundRepeat: "no-repeat, no-repeat",
        })},
    {label: <React.Fragment>logo_polska2050.png</React.Fragment>,
        value: JSON.stringify({
            backgroundImage: "url(logo_polska2050.png), " +
                "radial-gradient(circle at right, #fcd475, #ffffff)",
            backgroundPosition: "left top, right center",
            backgroundRepeat: "no-repeat, no-repeat",
        })},
    {label: <React.Fragment>logo_polska2050_mapa.png</React.Fragment>,
        value: JSON.stringify({
            backgroundImage: "url(logo_polska2050_top.png), url(logo_polska2050_bottom.png), url(logo_polska2050_middle.png)",
            backgroundPosition: "left top, left bottom, center center",
            backgroundRepeat: "no-repeat, no-repeat, repeat",
        })}
]

export const logo = (state: string = PREDEFINED_LOGOS[2].value, action: StringAction) => {
    switch (action.type) {
        case CHANGE_LOGO:
            return action.payload;
        default:
            return state;
    }
};

