import React from "react";
import {Box} from "@material-ui/core";
import {
    Area, AreaChart,
    Bar,
    BarChart,
    CartesianGrid,
    Legend,
    Line,
    LineChart,
    XAxis,
    YAxis
} from "recharts";
import {Wykres} from "./infografika/Wykres";

export interface WykresXYDane {
    label: string;
    dane0?: number;
    dane1?: number;
    dane2?: number;
    dane3?: number;
    dane4?: number;
    dane5?: number;
}

export class WykresXY extends React.Component<Wykres> {
    render() {
        const {dane_xy, opis, typ_wykresu = 'Liniowy (XY)', jednostka = "", kolory = 'paleta'} = this.props;
        if (opis === '') {
            return null;
        }
        let [tekst, serie_string] = opis?opis.split('|'):[];
        let serie = serie_string?serie_string.split(","):[];
        const COLOR_MAP = {
            "paleta": [
                [],
                ["#003f5c"],
                ["#003f5c", "#ffa600"],
                ["#003f5c", "#bc5090", "#ffa600"],
                ["#003f5c", "#7a5195", "#ef5675", "#ffa600"],
                ["#003f5c", "#58508d", "#bc5090", "#ff6361", "#ffa600"],
                ["#003f5c", "#444e86", "#955196", "#dd5182", "#ff6e54", "#ffa600"],
                ["#003f5c", "#374c80", "#7a5195", "#bc5090", "#ef5675", "#ff764a", "#ffa600"],
                ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"],
            ],
            "rozbiezne": [
                [],
                ["#de425b"],
                ["#488f31", "#de425b"],
            ],
            "odcienie": [
                [],
                ["#de425b"],
                ["#488f31", "#de425b"],
            ],
        };
        let colors = COLOR_MAP[kolory];
        const commonChartProps = {
            syncId: opis,
            width: 1200,
            height: 500,
            data: dane_xy,
            margin: {top: 10, right: 40, left: 45, bottom: 10}
        }

        let j = jednostka;
        if (jednostka && jednostka?.indexOf("auto") > -1) {
            let max = 0;
            dane_xy?.forEach(v => {
                if (v.dane0 && v.dane0 > max) max = v.dane0;
                if (v.dane1 && v.dane1 > max) max = v.dane1;
                if (v.dane2 && v.dane2 > max) max = v.dane2;
                if (v.dane3 && v.dane3 > max) max = v.dane3;
                if (v.dane4 && v.dane4 > max) max = v.dane4;
                if (v.dane5 && v.dane5 > max) max = v.dane5;
            });
            if (max > 1e9) {
                j = j.replace("auto", "mld");
            } else if (max > 1e6) {
                j = j.replace("auto", "mln");
            } else if (max > 1e3) {
                j = j.replace("auto", "tys.");
            } else {
                j = j.replace("auto", "");
            }
        }
        const divider = j.indexOf('tys.') > -1?1e3:
            (j.indexOf('mln') > -1?1e6:
                (j.indexOf('mld') > -1?1e9:1));
        const numberDivider = (value: number) => {
            return value / divider;
        }
        const grid = () => { return <CartesianGrid strokeDasharray="5 5"/> }
        const x = () => { return <XAxis dataKey="label" tickSize={12} tickMargin={10}/>}
        const y1 = () => { return <YAxis yAxisId="left" orientation="left" axisLine={{strokeWidth: 2}} tickSize={8} label={{value: j, position: "bottom", offset: 50}} tickFormatter={numberDivider}/> }
        const y2 = () => { return (opis && opis.indexOf(':R') > -1)?<YAxis yAxisId="right" orientation="right" axisLine={{strokeWidth: 2}} tickSize={18} label={{value: j, position: "bottom", offset: 50}} tickFormatter={numberDivider}/>:null }
        const legend = () => { return <Legend iconSize={20} wrapperStyle={{bottom: -10}}/> }
        return <Box><br/><Box margin="40px">{tekst}</Box>
            {typ_wykresu.indexOf("Kolumnowy") > -1?
                <BarChart {...commonChartProps}>
                    {grid()} {x()} {y1()} {y2()} {legend()}
                    {serie.map((value, index) => {
                        return value !== ''?<Bar key={index}
                                    name={value.replace(':R', ' (prawa oś)').replace(/:S:./, '')}
                                    stackId={value.indexOf(':S:') > -1?value.substr(value.indexOf(':S:') + 3, 1):undefined}
                                    dataKey={"dane" + index}
                                    fill={colors[serie.length][index]}
                                    yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                    })
                    }
                </BarChart>:
                opis.indexOf(":S") === -1?
                    <LineChart {...commonChartProps}>
                        {grid()} {x()} {y1()} {y2()} {legend()}
                        {serie.map((value, index) => {
                            return value !== ''?<Line key={index}
                                        type="monotone"
                                        name={value.replace(':R', ' (prawa oś)')}
                                        dataKey={"dane" + index}
                                        stroke={colors[serie.length][index]}
                                        fill={colors[serie.length][index]}
                                        strokeWidth={2}
                                        yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                        })
                        }
                    </LineChart>:
                    <AreaChart {...commonChartProps}>
                        {grid()} {x()} {y1()} {y2()} {legend()}
                        {serie.map((value, index) => {
                            return value !== ''?<Area key={index}
                                                      type="monotone"
                                                      stackId={value.indexOf(':S:') > -1?value.substr(value.indexOf(':S:') + 3, 1):undefined}
                                                      name={value.replace(':R', ' (prawa oś)').replace(/:S:./, '')}
                                                      dataKey={"dane" + index}
                                                      stroke={colors[serie.length][index]}
                                                      fill={colors[serie.length][index]}
                                                      strokeWidth={2}
                                                      yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                        })
                        }
                    </AreaChart>
            }
        </Box>;
    }
}