import React from "react";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import Dropdown from 'react-dropdown';
import {Box, Button, Container} from "@material-ui/core";
import { PREDEFINED_LOGOS } from "./konfiguracja/reducers";
import SelektorGminy from "./SelektorGminy";
import SelektorWojewodztwa from "./SelektorWojewodztwa";
import KopiujDoURL from "./KopiujDoURL";
import { ReactInstance } from "react";
import { changeLogo } from "./konfiguracja/actions";

interface UstawieniaProps extends PropsFromRedux {
    componentRef: React.RefObject<ReactInstance>
}

class UstawieniaInternal extends React.Component<UstawieniaProps> {
    render() {
        const {changeLogo, kod_gminy, gmina} = this.props;
        return <Box display="flex" flexDirection="column" className="accordionContent">
                    <span className="hint">Logo w nagłówku:</span>
                    <Dropdown
                        options={PREDEFINED_LOGOS}
                        value={PREDEFINED_LOGOS[2]}
                        onChange={(input)=>changeLogo(input.value)}
                        placeholder='Wybierz logo...'>
                    </Dropdown>
                    {/*<input type="text" name="logo_url" onChange={ev=>changeLogoUrl(ev.target.value)} value={logo_url}/>*/}
                    <strong>Wybór Jednostki Samorządu:</strong>
                    <span className="hint">Województwo:</span>
                    <SelektorWojewodztwa />
                    <span className="hint">Gmina:</span>
                    <SelektorGminy />
                    <Box display="flex" flexDirection="row" className="accordionContent">
                        <Container>
                            <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_ue#`} variant="outlined" color="primary">
                                Projekty UE
                            </Button>
                        </Container>
                        <Container>
                            <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_sunburst#`} variant="outlined" color="primary">
                                Wydatki
                            </Button>
                        </Container>
                        <Container>
                            <Button target="_blank" rel="noreferrer" href={`/panel.html?g=${kod_gminy}&n=${gmina}&l=0&f=pokaz_sunburst_dochody#`} variant="outlined" color="primary">
                                Dochody
                            </Button>
                        </Container>
                    </Box>
                    <KopiujDoURL componentRef={this.props.componentRef}/>
                </Box>
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        gmina: state.dane_gminy.gmina,
        kod_gminy: state.kod_gminy,
    };
};

const mapDispatchToProps = {
    changeLogo,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(UstawieniaInternal);