import {
    CHANGE_LOGO,
    CHANGE_CYTAT,
    CHANGE_AUTOR,
    CHANGE_LISTA_POWIATOW,
    CHANGE_KOD_GMINY,
    CHANGE_WOJEWODZTWO,
    CHANGE_MODULY, CHANGE_DZIAL
} from "../action";
import axios from "axios";
import {pobierzDaneGminy} from "../dane/actions";
import {CSSProperties, Dispatch} from "react";

export const changeModuly = (moduly: string[]) => ({
    type: CHANGE_MODULY,
    payload: moduly
})

export const changeDzial = (dzial: string) => ({
    type: CHANGE_DZIAL,
    payload: dzial
})

export const changeLogo = (logo: string) => ({
    type: CHANGE_LOGO,
    payload: logo
});

export const changeCytat = (cytat: string) => ({
    type: CHANGE_CYTAT,
    payload: cytat
});

export const changeAutor = (autor: string) => ({
    type: CHANGE_AUTOR,
    payload: autor
});

export interface PowiatEls {
    powiat: string,
    gminy: Gmina[]
}

export interface Gmina {
    gmina: string,
    kod_gminy: string
}

export const changeListaPowiatow = (listaPowiatow: PowiatEls[]) => ({
    type: CHANGE_LISTA_POWIATOW,
    payload: listaPowiatow
})

export const changeKodGminy = (kod_gminy: string) => ({
    type: CHANGE_KOD_GMINY,
    payload: kod_gminy
})

export const changeWojewodztwo = (wojewodztwo: string) => ({
    type: CHANGE_WOJEWODZTWO,
    payload: wojewodztwo
})

export const pobierzListePowiatow = (wojewodztwo: string, kod_gminy?: string) => {
    return function(dispatch: Dispatch<any>) {
        let wojName = wojewodztwo.replaceAll("Ś", "S").replaceAll("Ą", "A")
            .replaceAll("Ń", "N").replaceAll("Ł", "L")
            .replaceAll("Ó", "O").replaceAll("Ę", "E");
        const apiUrl = '/files/' + wojName + '.json';
        axios.get(apiUrl).then((response) => {
            const ret: PowiatEls[] = [];
            for (const x in response.data) {
                if (response.data.hasOwnProperty(x)) {
                    let gminy: Gmina[] = [];
                    for (const y in response.data[x]) {
                        if (response.data[x].hasOwnProperty(y)) {
                            gminy.push({gmina: y, kod_gminy: response.data[x][y]});
                        }
                    }
                    const p: PowiatEls = {powiat: x, gminy: gminy};
                    ret.push(p);
                }
            }
            dispatch(changeListaPowiatow(ret));
            const gminaDoPobrania = (kod_gminy === undefined?ret.values().next().value.gminy.values().next().value.kod_gminy:kod_gminy);
            dispatch(changeKodGminy(gminaDoPobrania));
            dispatch(pobierzDaneGminy(gminaDoPobrania));
        });
    }
}