import React from "react";
import ReactJoyride from "react-joyride";

interface Props {
    enabled: boolean;
    finishedCallback: any;
}

export class Tour extends React.Component<Props> {
    static STEPS = [
        {
            target: '.prawa',
            content: 'Generator pozwala Ci stworzyć grafikę do publikacji w mediach społecznościowych z danymi z dowolnej gminy',
            disableBeacon: true,
        },
        {
            target: '.acc-ustawienia',
            content: 'Tutaj możesz ustawić logo (podaj link do publicznie dostępnej grafiki), wybrać województwo i gminę a na koniec pobrać grafikę lub link aby podzielić się projektem z innymi',
        },
        {
            target: '.acc-komentarz',
            content: 'W tym miejscu dodasz komentarz (Twój, eksperta lub polityka) do infografiki',
        },
        {
            target: '.acc-budzet',
            content: 'Tutaj włączysz lub wyłączysz pokazywanie modułów dotyczących budżetu danej gminy oraz ustawisz ich opcje',
        },
        {
            target: '.acc-wykresy',
            content: 'A tutaj możesz dodać lub usunąć wykres. Pobierze on automatycznie dane z GUS, albo łatwo możesz wkleić własne',
        },
        {
            target: '.start-tour',
            content: 'Chcesz skorzystać z samouczka jeszcze raz? Kliknij tutaj. Klik, klik',
        },
    ]

    render () {
        const { enabled, finishedCallback } = this.props;
        // @ts-ignore
        const callback = data => {
            const { action } = data;
            if (action === "reset" || action === "close") {
                finishedCallback();
            }
            // TODO: Capture close, skip, next / prev action
        };
        return (
            <div className="app">
                <ReactJoyride run={enabled}
                              steps={Tour.STEPS} continuous={true} showSkipButton={true} showProgress={true}
                              callback={callback}
                              styles={{
                                  options: {
                                      primaryColor: '#f9c00c',
                                  },
                                  tooltipContainer: {
                                      textAlign: "justify",
                                  }
                              }}

                              locale={{
                                  next: "Dalej",
                                  last: "Zamknij",
                                  skip: "Pomiń",
                                  back: "Wstecz"
                              }}
                />
            </div>)
    }

}