import {CHANGE_DANE_GMINY, CHANGE_DANE_PODREGIONU, DaneGminyAction, DanePodregionuAction} from "../action";
import {DaneGminy} from "./DaneGminy";
import {DanePodregionu} from "./DanePodregionu";

export const initialDaneGminy: DaneGminy = {
    gmina: "",
    typ_gminy: "",
    grupyDzialy: [],
    grupyRozdzialy: [],
};

export const dane_gminy = (state: DaneGminy = initialDaneGminy, action: DaneGminyAction) => {
    switch (action.type) {
        case CHANGE_DANE_GMINY:
            return action.payload;
        default:
            return state;
    }
};

const initialDanePodregionu: DanePodregionu = {
    podregion: "",
    zgony: []
}

export const dane_podregionu = (state: DanePodregionu = initialDanePodregionu, action: DanePodregionuAction) => {
    switch (action.type) {
        case CHANGE_DANE_PODREGIONU:
            return action.payload;
        default:
            return state;
    }
};