export const wykresy = (state = initial_wykresy, action: WykresAction) => {
    switch (action.type) {
        case ADD_WYKRES:
            if (action.payload.wykres === undefined) {
                console.log("Brak wartosci dla ADD_WYKRES");
                return state;
            }
            return [...state, action.payload.wykres];
        case DELETE_WYKRES:
            if (action.payload.index === undefined) {
                console.log("Brak indexu dla DELETE_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, action.payload.index),
                ...state.slice(action.payload.index + 1)
            ];
        case SET_WYKRES:
            if (action.payload.index === undefined || action.payload.wykres === undefined) {
                console.log("Brak indexu lub gus_input dla SET_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, action.payload.index),
                {...(state[action.payload.index]), ...action.payload.wykres},
                ...state.slice(action.payload.index + 1)
        ];
        case MOVE_UP_WYKRES:
            if (action.payload.index === undefined || action.payload.index < 1) {
                console.log("Brak lub zly indeks dla MOVE_UP_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, action.payload.index - 1),
                ...state.slice(action.payload.index, action.payload.index + 1),
                ...state.slice(action.payload.index -1, action.payload.index),
                ...state.slice(action.payload.index + 1)
            ]

        case MOVE_DOWN_WYKRES:
            if (action.payload.index === undefined || action.payload.index > state.length -2) {
                console.log("Brak lub zly indeks dla MOVE_DOWN_WYKRES");
                return state;
            }
            return [
                ...state.slice(0, action.payload.index),
                ...state.slice(action.payload.index + 1, action.payload.index + 2),
                ...state.slice(action.payload.index, action.payload.index + 1),
                ...state.slice(action.payload.index + 2)
            ]
        default:
            return state;
    }
}
