import {DaneGminy} from "./dane/DaneGminy";
import {DanePodregionu} from "./dane/DanePodregionu";
import {Wykres} from "./infografika/Wykres";
import {PowiatEls} from "./konfiguracja/actions";

export const CHANGE_LOGO = "CHANGE_LOGO";
export const CHANGE_AUTOR = "CHANGE_AUTOR";
export const CHANGE_CYTAT = "CHANGE_CYTAT";
export const CHANGE_DANE_GMINY = "CHANGE_DANE_GMINY";
export const CHANGE_DANE_PODREGIONU = "CHANGE_DANE_PODREGIONU";
export const CHANGE_LISTA_POWIATOW = "CHANGE_LISTA_POWIATOW";
export const CHANGE_GRUPA_DZIAL = "CHANGE_GRUPA_DZIAL";
export const CHANGE_GRUPA_ROZDZIAL = "CHANGE_GRUPA_ROZDZIAL";
export const CHANGE_KOD_GMINY = "CHANGE_KOD_GMINY";
export const CHANGE_WOJEWODZTWO = "CHANGE_WOJEWODZTWO";
export const CHANGE_MODULY = "CHANGE_MODULY";
export const CHANGE_DZIAL = "CHANGE_DZIAL";

export const ADD_WYKRES = "ADD_WYKRES";
export const DELETE_WYKRES = "DELETE_WYKRES";
export const SET_WYKRES = "SET_WYKRES";
export const MOVE_DOWN_WYKRES = "MOVE_DOWN_WYKRES";
export const MOVE_UP_WYKRES = "MOVE_UP_WYKRES";

export interface StringAction {
    type: "CHANGE_LOGO" | "CHANGE_CYTAT" | "CHANGE_AUTOR" | "CHANGE_GRUPA_DZIAL" | "CHANGE_GRUPA_ROZDZIAL" | "CHANGE_WOJEWODZTWO" | "CHANGE_KOD_GMINY" | "CHANGE_DZIAL";
    payload: string;
}

export interface StringArrayAction {
    type: "CHANGE_MODULY";
    payload: string[];
}

export interface DaneGminyAction {
    type: "CHANGE_DANE_GMINY";
    payload: DaneGminy;
}

export interface DanePodregionuAction {
    type: "CHANGE_DANE_PODREGIONU";
    payload: DanePodregionu;
}

export interface ListaPowiatowAction {
    type: "CHANGE_LISTA_POWIATOW",
    payload: PowiatEls[];
}

export interface WykresAction {
    type: "ADD_WYKRES" | "DELETE_WYKRES" | "SET_WYKRES" | "MOVE_DOWN_WYKRES" | "MOVE_UP_WYKRES",
    payload: {wykres?: Wykres, index?: number}
}

export interface addWykresAction extends WykresAction {
    type: "ADD_WYKRES",
}

export interface deleteWykresAction extends WykresAction {
    type: "DELETE_WYKRES",
}

export interface setWykresAction extends WykresAction {
    type: "SET_WYKRES",
}

export interface moveDownWykresAction extends WykresAction {
    type: "MOVE_DOWN_WYKRES",
}

export interface moveUpWykresAction extends WykresAction {
    type: "MOVE_UP_WYKRES",
}
