export function format(value: number, unit: string) {
    let jednostka = '';
    let v = 0;
    if (unit.indexOf("auto") > -1) {
        if (value > 1e9) {
            v = Math.round(value / 1e7) / 100;
            jednostka = unit.replace("auto", "mld");
        } else if (value > 1e6) {
            v = Math.round(value / 1e4) / 100;
            jednostka = unit.replace("auto", "mln");
        } else if (value > 1e3) {
            v = Math.round(value / 10) / 100;
            jednostka = unit.replace("auto", "tys.");
        } else {
            jednostka = unit.replace("auto", "");
            v = value;
        }
    } else {
        v = unit.indexOf('tys.') > -1 ? Math.round(value / 1000) :
            (unit.indexOf('mln') > -1 ? Math.round(value / 1000000):value);
        jednostka = unit
    }
    return v + ' ' + jednostka;

}