import React from "react";

interface SelektorGrupyProps {
    grupaChangeFn: (grupa: string) => void;
}

const GRUPY = [
    "Wynagrodzenia i składki",
    "Wydatki na zadania statutowe",
    "Dotacje na zadania bieżące",
    "Świadczenia na rzecz osób fizycznych",
    "Wynagrodzenia finansowane programami UE",
    "Współfinansowanie wynagrodzeń programów UE",
    "Wynagrodzenia dla innych programów zagranicznych",
    "Wydatki finansowane programami UE",
    "Współfinansowanie wydatków programów UE",
    "Wydatki dla innych programów zagranicznych",
    "Dotacje bieżące z programów UE",
    "Współfinansowanie dotacji bieżących z programów UE",
    "Dotacje bieżące z innych programów zagranicznych",
    "Świadczenia dla osób fizycznych finansowane z UE",
    "Współfinansowane świadczeń dla osób fizycznych z UE",
    "Świadczenia dla osób fizycznych z innych programów zagranicznych",
    "Wypłaty z poręczeń i gwarancji udzielonych przez gminę",
    "Obsługa długu",
    "Inwestycje ze środków gminy",
    "Inwestycje ze środków UE",
    "Współfinansowanie inwestycji ze środków UE",
    "Dotacje do inwestycji ze środków gminy",
    "Dotacje do inwestycji ze środków UE",
    "Współfinansowanie dotacji do inwestycji ze środków UE",
    "Zakup i objęcie akcji i udziałów",
    "Wniesienie wkładów do spółek prawa handlowego",
]

export class SelektorGrupy extends React.Component<SelektorGrupyProps> {
    render() {
        return <select
            id="SelektorGrupy"
            style={{marginBottom: 10}}
            onChange={ev => this.props.grupaChangeFn(ev.target.value)}>
            {GRUPY.map((name) => (
                <option key={name} value={name}>
                    {name}
                </option>
            ))}
        </select>;
    }
}
