import React from "react";
import Box from "@material-ui/core/Box";
import {AttachMoney, DeleteSharp} from "@material-ui/icons";
import {Budzet, BudzetComp} from "./BudzetComp";
import {StylesProvider} from "@material-ui/core";
import {DotacjeUEComp} from "./DotacjeUEComp";
import {Zgony} from "./Zgony";
import {WykresXY} from "./WykresXY";
import {WykresKolowy} from "./WykresKolowy";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import Naglowek from "./Naglowek";
import {Wykres} from "./infografika/Wykres";


class PanelInternal extends React.Component<PropsFromRedux> {
    renderBudzet(budzet?: Budzet) {
        return this.isShown('budzet') && budzet?<React.Fragment>
                <Box className="intro">Budżet za rok <strong>2019</strong></Box>
                <Box display="flex" flexDirection="row">
                    {/*<Money className="icon"></Money>*/}
                    <AttachMoney fontSize="large" className="icon" viewBox=""/>
                    <BudzetComp dane={budzet}/>
                </Box><br/>
            </React.Fragment>:<React.Fragment/>;
    }

    renderSmieci(gospodarka_odpadami_komunalnymi?: Budzet) {
        return this.isShown('smieci') && gospodarka_odpadami_komunalnymi?
            <React.Fragment>
                <Box className="intro">Gospodarka Odpadami Komunalnymi</Box>
                <Box display="flex" flexDirection="row">
                    <DeleteSharp fontSize="large" className="icon" viewBox="" />
                    <BudzetComp dane={gospodarka_odpadami_komunalnymi}/>
                </Box><br/>
            </React.Fragment>:<React.Fragment/>;
    }

    isShown(name: string) {
        return (this.props.moduly.indexOf(name) > -1)
    }

    componentDidUpdate(prevProps: Readonly<PropsFromRedux>, prevState: Readonly<{}>, snapshot?: any) {
        const div = document.getElementById('main_section');
        const warning = document.getElementById('warning');
        // @ts-ignore
        if (div.offsetHeight > 1.5 * div.offsetWidth) {
            // @ts-ignore
            warning.innerText = "Wysokość infografiki jest większa niż 150% szerokości i nie będzie dobrze " +
                "wyglądać na FB! Doradzamy usunięcie niektórych elementów!";
        } else {
            // @ts-ignore
            warning.innerText = "";
        }
    }

    render() {
        const {autor, cytat, dane_gminy, dane_podregionu, dzial, grupa_dzial, grupa_rozdzial, logo, wykresy} = this.props;
        const {podregion, zgony} = dane_podregionu;
        const {gospodarka_odpadami_komunalnymi, budzet, dotacje_ue, children} = dane_gminy || {};
        // W UI ustawiamy od jeden ale indeks jest od zera, stąd odejmujemy 1.
        const wybranyDzial = children ? children[parseInt(dzial || '1') - 1] : null;
        const caloscWydatkow = children?.reduce((a, b) => a + b.value, 0) || 1;
        const dzialCzescCalosci = ((wybranyDzial?.value || 0) * 100) / caloscWydatkow;
        const grupyDzialyCzescCalosci = dane_gminy && dane_gminy.grupyDzialy && grupa_dzial && dane_gminy.grupyDzialy[grupa_dzial]?
            // @ts-ignore
            ((dane_gminy.grupyDzialy[grupa_dzial].reduce((a, b) => a + b.value, 0)) * 100 / caloscWydatkow):0;
        const grupyRozdzialyCzescCalosci = dane_gminy && dane_gminy.grupyRozdzialy && grupa_rozdzial && dane_gminy.grupyRozdzialy[grupa_rozdzial]?
            // @ts-ignore
            ((dane_gminy.grupyRozdzialy[grupa_rozdzial].reduce((a, b) => a + b.value, 0)) * 100 / caloscWydatkow):0;
        const dzialChildren = wybranyDzial ? wybranyDzial.children : [];

        return (
            <StylesProvider injectFirst>
                <div id="main_section" style={JSON.parse(logo)}>
                    <Box display="flex" flexDirection="column">
                        <Naglowek />
                        <Box className="tlo">
                            {cytat !== '' ? cytat.split("\n").map(line => {return <Box margin="40px"><i>{line}</i><br/></Box>}) : ''}
                            {autor !== '' ?
                                <Box className="autor">{autor}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Box> : ''}
                            {this.renderBudzet(budzet)}
                            {this.renderSmieci(gospodarka_odpadami_komunalnymi)}
                            {this.isShown('dotacje_ue') && dotacje_ue?
                                 <DotacjeUEComp dotacje_ue={dotacje_ue}/>:
                                 <React.Fragment />}
                            {this.isShown('budzet-wykres') && dane_gminy && dane_gminy.budzet_wykres?
                                <WykresXY dane_xy={dane_gminy.budzet_wykres} typ_wykresu="Kolumnowy (XY)"
                                          opis="Historia budżetu gminy:|dochody,wydatki" jednostka="auto zł" kolory="rozbiezne"/> :null}
                            {this.isShown('budzet-bilans') && dane_gminy && dane_gminy.budzet_wykres?
                                <WykresXY dane_xy={dane_gminy.budzet_wykres} typ_wykresu="Kolumnowy (XY)"
                                          opis="Bilans budżetu gminy:|,,budżet:S:A,nadwyżka:S:A,deficyt:S:A" jednostka="auto zł"/> :null}
                            {this.isShown('oswiata-wykres') && dane_gminy && dane_gminy.oswiata_wykres?
                                <WykresXY dane_xy={dane_gminy.oswiata_wykres} typ_wykresu="Kolumnowy (XY)"
                                          opis="Subwencja oświatowa a rzeczywiste wydatki gmin na oświatę:|subwencja,wydatki" jednostka="auto zł" kolory="rozbiezne"/> :null}
                            {this.isShown('dzialy') && children?
                                    <WykresKolowy dane_kolowy={children} opis="Wydatki wg działów budżetu:" procent_calosci={100} jednostka="auto zł" typ_wykresu="Pierścieniowy"/>:<React.Fragment />}
                            {this.isShown('rozdzialy') && dzialChildren && wybranyDzial?
                                    <WykresKolowy dane_kolowy={dzialChildren} opis={"Rozdział wydatków z działu <b>" + wybranyDzial.name + "</b>:"} procent_calosci={dzialCzescCalosci} jednostka="auto zł" typ_wykresu="Pierścieniowy"/>:<React.Fragment />
                            }
                            {this.isShown('grupy-dzialy') && dane_gminy && grupa_dzial && dane_gminy.grupyDzialy && dane_gminy.grupyDzialy[grupa_dzial]?
                                    <WykresKolowy dane_kolowy={dane_gminy.grupyDzialy[grupa_dzial]} opis={"Wydatki z grupy <b>" + grupa_dzial + "</b> wg działu:"} procent_calosci={grupyDzialyCzescCalosci} jednostka="auto zł" typ_wykresu="Pierścieniowy"/>:<React.Fragment />}
                            {this.isShown('grupy-rozdzialy') && dane_gminy && grupa_rozdzial && dane_gminy.grupyRozdzialy && dane_gminy.grupyRozdzialy[grupa_rozdzial]?
                                    <WykresKolowy dane_kolowy={dane_gminy.grupyRozdzialy[grupa_rozdzial]} opis={"Wydatki z grupy <b>" + grupa_rozdzial + "</b> wg rozdziału:"} procent_calosci={grupyRozdzialyCzescCalosci} jednostka="auto zł" typ_wykresu="Pierścieniowy"/>:<React.Fragment />}
                            {this.isShown('zgony') && zgony?
                                <Zgony zgony={zgony} podregion={podregion}/>:<React.Fragment />}
                            {wykresy.map((entry: Wykres, index: number) => {
                                switch (entry.typ_wykresu) {
                                    case "Pierścieniowy":
                                    case "Kołowy":
                                        return <WykresKolowy key={index} {...entry}/>
                                    case "Kolumnowy (XY)":
                                    case "Liniowy (XY)":
                                    case "GUS: Liniowy":
                                    case "GUS: Kolumnowy":
                                    default:
                                        return <WykresXY key={index} {...entry}/>
                                }
                            })}
                            <Box>&nbsp;</Box>
                            <Box className="stopka">Zobacz te dane dla Twojej gminy: infografika.edu.pl/<span id="hash"></span>&nbsp;&nbsp;</Box>
                            <Box className="zrodlo">&nbsp;Źródło: dane.gov.pl, GUS</Box>
                        </Box>
                    </Box>

                    {/*            <span className="intro" hidden>Dochody z UE: <span id="dochod_ue"*/}
                    {/*                                                               className="pomaranczowy"></span></span><br/>*/}
                    {/*            <span className="intro">Wydatki dofinansowane z UE:<br/>*/}
                    {/*Bieżące gminy: <span id="wydatki_biezace_ue" className="pomaranczowy"></span><br/>*/}
                    {/*Inwestycyjne gminy: <span id="wydatki_inwestycje_ue" className="pomaranczowy"></span><br/>*/}

                    {/*            <span id="janosik:otrzymala" className="intro">Gmina otrzymała <span id="janosik:dochody"*/}
                    {/*                                                                                 className="pomaranczowy"></span>*/}
                    {/*    "janosikowego" od samorządów z wysokimi dochodami</span>*/}
                    {/*            <span id="janosik:wplacila" className="intro">Gmina wpłaciła <span id="janosik:wydatki"*/}
                    {/*                                                                               className="pomaranczowy"></span>*/}
                    {/*    netto "janosikowego" na rzecz samorządów z niskimi dochodami</span><br/><br/><br/>*/}
                </div>
            </StylesProvider>
        );
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        autor: state.autor,
        cytat: state.cytat,
        dane_gminy: state.dane_gminy,
        dane_podregionu: state.dane_podregionu,
        dzial: state.dzial,
        grupa_dzial: state.grupa_dzial,
        grupa_rozdzial: state.grupa_rozdzial,
        logo: state.logo,
        moduly: state.moduly,
        wykresy: state.wykresy,
    };
};

// forwardRef jest potrzebne, aby móc pobrać referencje na właściwy komponent Panel z App.tsx do screenshot'a
const connector = connect(mapStateToProps, null, null, { forwardRef: true });

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(PanelInternal);