import {DaneGminy} from "./DaneGminy";
import {CHANGE_DANE_GMINY, CHANGE_DANE_PODREGIONU} from "../action";
import axios from "axios";
import {Dispatch} from "react";
import {DanePodregionu} from "./DanePodregionu";
import {WykresXYDane} from "../WykresXY";
import {setWykres} from "../infografika/actions";
import {Wykres} from "../infografika/Wykres";

export const pobierzDaneGminy = (kodGminy: string) => {
    return function (dispatch: Dispatch<any>, getState: any) {
        const apiUrl = '/files/' + kodGminy + '-panel.json';
        axios.get(apiUrl).then((response) => {
            dispatch(changeDaneGminy(response.data));
            if (response.data.podregion_eurostat) {
                dispatch(pobierzZgony(response.data.podregion_eurostat));
            }
            if (response.data.kod_gus) {
                getState().wykresy.forEach((input: Wykres, index: number) => {
                    dispatch(pobierzDaneGUS(index, response.data.kod_gus || '', input.gus_ids || '', input.opis || ''));
                });
            }
        });
    }
}

export const pobierzZgony = (podregion: string) => {
    return function(dispatch: Dispatch<any>) {
        const apiUrl = '/files/' + podregion + '.json';
        axios.get(apiUrl).then((response) => {
            dispatch(changeDanePodregionu(response.data));
        });
    }
}

export const pobierzDaneGUS = (index: number, kodGus: string, ids_string: string, opis: string) => {
    return function(dispatch: Dispatch<any>) {
        if (ids_string === '') {
            return null;
        }
        let ids = ids_string.split(",").map(x => parseInt(x));
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        ids.forEach(id => {
            apiUrl += "&var-id=" + id;
        });
        axios.get(apiUrl).then((response) => {
            let dane: WykresXYDane[] = [{label: "2015"}, {label: "2016"}, {label: "2017"}, {label: "2018"}, {label: "2019"}];
            for (let r of response.data.results) {
                for (let i = 0; i < ids.length; i++) {
                    // znajdzmy odpowiedni id do przypisania wartosci, aby byly w takiej kolejnosci jak na liscie
                    if (ids[i] !== r.id) {
                        continue;
                    }
                    for (let v of r.values) {
                        for (let z of dane) {
                            if (z.label === v.year) {
                                // @ts-ignore
                                z["dane" + i] = v.val;
                            }
                        }
                    }
                }
            }
            const wykres = {opis: opis, gus_ids: ids_string, dane_xy: dane};
            dispatch(setWykres(index, wykres));
        });
    }
}

export const changeDaneGminy = (dane_gminy: DaneGminy) => ({
    type: CHANGE_DANE_GMINY,
    payload: dane_gminy
});

export const changeDanePodregionu = (dane_podregionu: DanePodregionu) => ({
    type: CHANGE_DANE_PODREGIONU,
    payload: dane_podregionu
})