import {connect, ConnectedProps} from 'react-redux'
import React, {FormEvent} from 'react';
import './App.css';

import Box from "@material-ui/core/Box";
import Panel from "./Panel";
import WyborModulow from "./WyborModulow";
import {SelektorGrupy} from "./SelektorGrupy";
import {
    changeAutor,
    changeCytat, changeDzial,
    pobierzListePowiatow
} from "./konfiguracja/actions";
import {RootState} from "./reducers";
import {
    changeGrupaDzial,
    changeGrupaRozdzial,
} from "./infografika/actions";
import Wykresy from "./Wykresy";
import {Accordion, AccordionSummary, Modal} from "@material-ui/core";
import {ExpandMore} from "@material-ui/icons";
import {Tour} from "./Tour";
import 'react-dropdown/style.css';
import { createStyles, Theme, withStyles } from "@material-ui/core/styles";
import Ustawienia from './Ustawienia';

const styles = (theme: Theme) => createStyles({
    root: {
      backgroundColor: "red"
    }
  });

interface AppState {
    pomocOtwarta: boolean;
    tour: boolean;
    componentRef : React.RefObject<any>;
}

class App extends React.Component<PropsFromRedux, AppState> {
    classes : any;

    constructor(props: PropsFromRedux) {
        super(props);
        this.state = {pomocOtwarta: false, tour: false, componentRef: React.createRef()};

        this.handleSubmit = this.handleSubmit.bind(this);

    }

    handleSubmit(event: FormEvent) {
        event.preventDefault();
    }

    isShown(name: string) {
        return (this.props.moduly.indexOf(name) > -1)
    }

    renderPomocModal() {
        const zamknijPomoc = () => {
            this.setState( {pomocOtwarta: false});
        }

        return <Modal open={this.state.pomocOtwarta} onClose={zamknijPomoc}>
            <div className="modalStyle paper">
                <div>Problem z generowaniem infografiki?<br/><br/>
                    Napisz do nas: <a href="mailto:pomoc@infografika.edu.pl">pomoc@infografika.edu.pl</a><br/><br/></div>
                <div className="zamknijPomoc"><button onClick={(ev) => zamknijPomoc()}>Zamknij</button></div>
            </div>
        </Modal>

    }

    render() {
        const otworzPomoc = () => {
            this.setState({...this.state, pomocOtwarta: true});
        }

        const {autor, cytat, dzial} = this.props;
        const {changeAutor, changeCytat, changeDzial} = this.props;
        return (
            <Box display="flex" flexDirection="row">
                <Panel ref={this.state.componentRef} />
                <form onSubmit={this.handleSubmit} className="formularzContainer">
    <Box display="flex" flexDirection="column" className="formularz">
        <div id="warning"></div>
        {this.renderPomocModal()}
        <Tour enabled={this.state.tour} finishedCallback={() => this.setState({...this.state, tour: false})}/>
        <a className="start-tour pomocLink" href="javascript:;" onClick={() => {this.setState({...this.state, tour: true});}}>otwórz samouczek</a>
        <Accordion defaultExpanded={true} className="acc-ustawienia">
            <AccordionSummary className="accordion" expandIcon={<ExpandMore />}>Ustawienia Infografiki</AccordionSummary>
                <Ustawienia componentRef={this.state.componentRef} />
        </Accordion>
        <Accordion defaultExpanded={true} className="acc-komentarz">
            <AccordionSummary className="accordion" expandIcon={<ExpandMore />}>Komentarz Eksperta</AccordionSummary>
            <Box display="flex" flexDirection="column" className="accordionContent" >
                <span className="hint">Tekst:</span>
                <textarea name="cytat" onChange={ev=>changeCytat(ev.target.value)} value={cytat}/>
                <span className="hint">Autor:</span>
                <input type="text" name="autor" onChange={ev=>changeAutor(ev.target.value)} value={autor}/>
            </Box>
        </Accordion>
        <Accordion defaultExpanded={true} className="acc-budzet">
            <AccordionSummary className="accordion" expandIcon={<ExpandMore />}>Moduły Budżetowe</AccordionSummary>
            <Box display="flex" flexDirection="column" className="accordionContent" >
                <span className="hint">Wybór modułów do wyświetlenia:</span>
                <WyborModulow />
                {this.isShown('rozdzialy')?<React.Fragment>
                        <strong>Number działu do szczegółowego wyświetlenia:</strong>
                        <input type="text" name="dzial" value={dzial}
                               onChange={ev=>changeDzial(ev.target.value)}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
                {this.isShown('grupy-dzialy')?<React.Fragment>
                        <strong>Podział wydatków z grupy na działy:</strong>
                        <SelektorGrupy grupaChangeFn={this.props.changeGrupaDzial}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
                {this.isShown('grupy-rozdzialy')?<React.Fragment>
                        <strong>Podział wydatków z grupy na rozdziały:</strong>
                        <SelektorGrupy grupaChangeFn={this.props.changeGrupaRozdzial}/>
                        <hr/></React.Fragment>:<React.Fragment/>}
            </Box>
        </Accordion>
        <Accordion defaultExpanded={true} className="acc-wykresy">
            <AccordionSummary className="accordion" expandIcon={<ExpandMore />}>Wykresy</AccordionSummary>
            <Box display="flex" flexDirection="column" className="accordionContent wykresy" >
                <Box><strong><a target="_blank" rel="noreferrer" href="https://bdl.stat.gov.pl/BDL/metadane/cechy/2398#">Dane dostępne z GUS</a></strong></Box>
                <span className="hint" style={{color: "red"}}>(Puste wykresy GUS to zwykle awaria ich serwera - ostatnio 3.I.21)</span>
                <Wykresy/>
            </Box>
        </Accordion>
        <a className="pomocLink" href="javascript:;" onClick={() => {otworzPomoc();}}>pomoc</a>
    </Box>
                </form>
            </Box>
        );
    }

    componentDidMount() {
        this.props.pobierzListePowiatow(this.props.wojewodztwo, this.props.kod_gminy);
        setTimeout(() => this.setState({...this.state, tour: true}), 5000);
        this.resize();
        window.addEventListener("resize", this.resize);
    }

    resize() {
        if (window.innerWidth < 800) {
            // @ts-ignore
            document.getElementById("root").className = "super_male_okno";
        } else if (window.innerWidth < 1500) {
            // @ts-ignore
            document.getElementById("root").className = "male_okno";
        } else {
            // @ts-ignore
            document.getElementById("root").className = "";
        }
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        autor: state.autor,
        cytat: state.cytat,
        kod_gminy: state.kod_gminy,
        logo: state.logo,
        dane_gminy: state.dane_gminy,
        wojewodztwo: state.wojewodztwo,
        lista_powiatow: state.lista_powiatow,
        moduly: state.moduly,
        dzial: state.dzial,
    };
};

const mapDispatchToProps = {
    changeAutor,
    changeCytat,
    changeDzial,
    changeGrupaDzial,
    changeGrupaRozdzial,
    pobierzListePowiatow,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(withStyles(styles, { withTheme: true})(App));