import {Wykres} from "./infografika/Wykres";
import React from "react";
import Box from "@material-ui/core/Box";
import {
    AddCircleOutline,
    ArrowDownward,
    ArrowUpward,
    CancelRounded,
    DonutLarge,
    Equalizer,
    ShowChart,
    PieChart
} from "@material-ui/icons";
import {DebounceInput} from "react-debounce-input";
import {RootState} from "./reducers";
import {
    addWykres,
    deleteWykres,
    moveDownWykres,
    moveUpWykres,
    setWykres
} from "./infografika/actions";
import {pobierzDaneGUS} from "./dane/actions";
import {connect, ConnectedProps} from "react-redux";
import {WykresXYDane} from "./WykresXY";
import {NameValue} from "./WykresKolowy";

class WykresyInternal extends React.Component<PropsFromRedux> {
    csvToWykresXYDaneArray(csv: string) {
        let lines = csv.split('\n');
        let result = [];
        for(let i = 0;i < lines.length;i++) {
            if (lines[i].length === 0) {
                continue;
            }
            let line = lines[i].split(",");
            let d0 = isNaN(Number(line[1]))?undefined:Number(line[1]);
            let d1 = isNaN(Number(line[2]))?undefined:Number(line[2]);
            let d2 = isNaN(Number(line[3]))?undefined:Number(line[3]);
            let d3 = isNaN(Number(line[4]))?undefined:Number(line[4]);
            let d4 = isNaN(Number(line[5]))?undefined:Number(line[5]);
            let d5 = isNaN(Number(line[6]))?undefined:Number(line[6]);
            result.push({label: line[0],
                dane0: d0,
                dane1: d1,
                dane2: d2,
                dane3: d3,
                dane4: d4,
                dane5: d5,
            });
        }
        return result;
    }

    wykresXYDaneArrayToCsv(json: WykresXYDane[]) {
        let ret = '';
        json.forEach((nameValue) => {ret += nameValue.label + ',' + (nameValue.dane0?nameValue.dane0:'') + ',' + (nameValue.dane1?nameValue.dane1:'') + ',' + (nameValue.dane2?nameValue.dane2:'') + ',' + (nameValue.dane3?nameValue.dane3:'') + ',' + (nameValue.dane4?nameValue.dane4:'') + ',' + (nameValue.dane5?nameValue.dane5:'') + '\n'});
        return ret;
    }

    csvToNameValueArray(csv: string) {
        let lines = csv.split('\n');
        let result = [];
        for(let i = 0;i < lines.length;i++) {
            let line = lines[i].split(",");
            if (line.length === 2) {
                let v = isNaN(Number(line[1]))?0:Number(line[1]);
                result.push({name: line[0], value: v});
            }
        }
        return result;
    }

    nameValueArrayToCsv(json: NameValue[]) {
        let x = '';
        json.forEach((nameValue) => {x += nameValue.name + ',' + nameValue.value + '\n'});
        return x;
    }

    renderChartType(index: number, wykres: Wykres, hint: string) {
        const {setWykres} = this.props;
        let x: any;
        switch (wykres.typ_wykresu) {
            case "Liniowy (XY)":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Kolumnowy (XY)"})}><Equalizer/></button>
                break;
            case "Kolumnowy (XY)":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Liniowy (XY)"})}><ShowChart/></button>
                break;
            case "GUS: Liniowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "GUS: Kolumnowy"})}><Equalizer/></button>
                break;
            case "GUS: Kolumnowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "GUS: Liniowy"})}><ShowChart/></button>
                break;
            case "Kołowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Pierścieniowy"})}><DonutLarge/></button>
                break;
            case "Pierścieniowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Kołowy"})}><PieChart/></button>
                break;
        }
        return <React.Fragment>
            <Box display="flex" flexDirection="row">
                {x}
                <Box display="flex" flexDirection="column">
                    <span className="hint"> <b>Wykres {index}</b></span>
                    <span className="hint">{hint}</span>
                </Box>
            </Box>
            <DebounceInput type="text" name="opis" value={wykres.opis}
                           onChange={ev=>setWykres(index, {opis: ev.target.value.trim()})}
                           debounceTimeout={2000}/>
        </React.Fragment>
    }

    renderWykres(index: number, wykres: Wykres, kod_gus: string) {
        const {pobierzDaneGUS, setWykres} = this.props;
        let hint = "";
        switch (wykres.typ_wykresu) {
            // @ts-ignore Intentional fall-through
            case "GUS: Liniowy":
                hint = "Format: NAGŁÓWEK|SERIA1,SERIA2:R (\":R\" przypisuje serię do prawej osi Y, a \":S:A\" zmienia wykres liniowy na powierzchniowy i pokazuje wartości kumulatywnie)";
            case "GUS: Kolumnowy":
                if (hint === "") {
                    hint = "Format: NAGŁÓWEK|SERIA1,SERIA2:R (\":R\" przypisuje serię do prawej osi Y, a \":S:A\" grupuje kolumny)";
                }
                return <React.Fragment>
                    {this.renderChartType(index, wykres, hint)}
                    <span className="hint">Identyfikatory danych z GUS. Dane muszą być dostępne na poziomie gminy.</span>
                    <DebounceInput type="text" name="gus_ids" value={wykres.gus_ids}
                                   onChange={ev=>pobierzDaneGUS(index, kod_gus, ev.target.value.trim(), wykres.opis)}
                                   debounceTimeout={2000}/>
                </React.Fragment>
            case "Kolumnowy (XY)":
            case "Liniowy (XY)":
                return <React.Fragment>
                    {this.renderChartType(index, wykres, "Nagłówek wykresu na bazie własnych danych:")}
                    <DebounceInput element="textarea" forceNotifyByEnter={false} debounceTimeout={2000} className="json" name="wlasny_liniowy.dane"
                                   onChange={ev=>setWykres(index, {dane_xy: this.csvToWykresXYDaneArray(ev.target.value)})}
                                   value={this.wykresXYDaneArrayToCsv(wykres.dane_xy || [])}/>
                    <span className="hint">Jednostka osi Y. Jeśli zawiera 'tys.' lub 'mln' to zostanie odpowiednio przeliczona:</span>
                    <DebounceInput type="text" name="wlasny_liniowy.jednostka" onChange={ev=>setWykres(index, {jednostka: ev.target.value})}
                           value={wykres.jednostka} debounceTimeout={2000}/>
                </React.Fragment>
            case "Kołowy":
            case "Pierścieniowy":
                return <React.Fragment>
                    {this.renderChartType(index, wykres, "Nagłówek wykresu:")}
                    <span className="hint">Dane do wykresu:</span>
                    <DebounceInput element="textarea" forceNotifyByEnter={false} debounceTimeout={2000} className="json" name="wlasny.dane"
                                   onChange={ev=>setWykres(index, {dane_kolowy: this.csvToNameValueArray(ev.target.value)})} value={this.nameValueArrayToCsv(wykres.dane_kolowy || [])}/>
                    <span className="hint">Jednostka osi Y. Jeśli zawiera 'tys.' lub 'mln' to zostanie odpowiednio przeliczona:</span>
                    <DebounceInput type="text" name="wlasny.jednostka" onChange={ev=>setWykres(index, {jednostka: ev.target.value})}
                                   value={wykres.jednostka} debounceTimeout={2000}/>
                </React.Fragment>
        }
        return null;
    }

    render() {
        const {dane_gminy, wykresy} = this.props;
        const {moveUpWykres, deleteWykres, moveDownWykres, addWykres} = this.props;

        return (<React.Fragment>
            {wykresy.map((input: Wykres, index: number) => {
                return <React.Fragment key={index}>
                    <Box display="flex" flexDirection="row" className={index%2===0?"odd":"even"}>
                        <Box display="flex" flexDirection="column" className="panelIkon">
                            <button className="ikonaWykresu" onClick={ev=>moveUpWykres(index)} style={{visibility: index > 0?"visible":"hidden"}}><ArrowUpward/></button>
                            <button className="ikonaWykresu czerwony" onClick={ev=>deleteWykres(index)}><CancelRounded/></button>
                            <button className="ikonaWykresu" onClick={ev=>moveDownWykres(index)} style={{visibility: index < wykresy.length - 1?"visible":"hidden"}}><ArrowDownward/></button>
                        </Box>
                        <Box display="flex" flexDirection="column">
                            {this.renderWykres(index, input, dane_gminy.kod_gus || '')}
                        </Box>
                    </Box>
                </React.Fragment>})}
                <Box display="flex" flexDirection="row" className={"panelDodania " + ((wykresy.length)%2===0?"odd":"even")}>
                    Dodaj wykres:
                    <button className="ikonaWykresu" onClick={ev=>addWykres({opis: "Wykres XY|seria1:S:A, seria2:S:A",
                        dane_xy: this.csvToWykresXYDaneArray("2015,24,65\n2016,34,55\n2017,54,35\n2018,74,15\n2019,104,3"), typ_wykresu: "Liniowy (XY)"})}><AddCircleOutline/><Equalizer/></button>
                    <button className="ikonaWykresu" onClick={ev=>addWykres({opis: "Sondaż IBRiS dla RP z 11.01.2021",
                        dane_kolowy: this.csvToNameValueArray("PiS,33.2\nKO,20.6\nPL_2050,12.6\nLewica,7.7\nKonfederacja,6.4\nPSL,5.2"), typ_wykresu: "Kołowy"})}><AddCircleOutline/><PieChart/></button>
                    <button className="ikonaWykresu" onClick={ev=>addWykres({opis: "Emisja zanieczyszczeń (t/r)|gazowych bez CO2,pyłowych:R",
                        gus_ids: "1509,1508", typ_wykresu: "GUS: Liniowy"})}><AddCircleOutline/>GUS</button>
                </Box>
            </React.Fragment>);
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        wykresy: state.wykresy,
        dane_gminy: state.dane_gminy,
    };
};

const mapDispatchToProps = {
    pobierzDaneGUS,
    moveUpWykres,
    deleteWykres,
    moveDownWykres,
    setWykres,
    addWykres,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(WykresyInternal);